export const mVMixin = {
  methods: {
    getRangeFormat (date) {
      let day = date.getDate()
      let month = date.getMonth() + 1
      let year = date.getFullYear()
      if (month < 10) {
        month = '0' + month
      }
      if (day < 10) {
        day = '0' + day
      }
      return day + '/' + month + '/' + year
    },
    formatDate (timeStr) {
      let temp = new Date(timeStr)
      let newFormat = ''
      if (temp instanceof Date && !isNaN(temp)) {
        let  monthNames = ['JAN', 'FEB', 'MAR', 'APR', 'MAY', 'JUN', 'JUL', 'AUG', 'SEP', 'OCT', 'NOV', 'DEC']
        let date = temp.getDate()
        let month = monthNames[temp.getMonth()]
        let year = temp.getFullYear()
        if (date < 10) {
          date = '0' + '' + date
        }
        newFormat = date + ' ' + month + ' ' + year
      } else {
        temp = timeStr.split(/(\s+)/)[0].split('-').reverse()
        let month = ['JAN', 'FEB', 'MAR', 'APR', 'MAY', 'JUN', 'JUL', 'AUG', 'SEP', 'OCT', 'NOV', 'DEC']
        temp[1] = month[parseInt(temp[1]) - 1]
        newFormat = temp.join(' ')
        if (newFormat.charAt(0) === '0') {
          newFormat = newFormat.slice(1)
        }
      }
      return newFormat
    },
    formatDateTime (timeStr) {
      let temp = new Date(timeStr)
      let newFormat = ''
      let timeFormat = ''
      if (temp instanceof Date && !isNaN(temp)) {
        let  monthNames = ['JAN', 'FEB', 'MAR', 'APR', 'MAY', 'JUN', 'JUL', 'AUG', 'SEP', 'OCT', 'NOV', 'DEC']
        let date = temp.getDate()
        let month = monthNames[temp.getMonth()]
        let year = temp.getFullYear()
        let temp1 = ''
        if (date < 10) {
          date = '0' + '' + date
        }
        newFormat = date + '-' + month + '-' + year
        if (timeStr.split(' ')[1]) {
          temp1 = timeStr.split(' ')[1]
        } else if (timeStr.split('T')[1]) {
          temp1 = timeStr.split('T')[1]
          temp1 = temp1.split('.')[0]
        }
        timeFormat = temp1
      } else {
        temp = timeStr.split(/(\s+)/)[0].split('-').reverse()
        let temp1 = ''
        if (timeStr.split(/(\s+)/)[2]) {
          temp1 = timeStr.split(/(\s+)/)[2]
        }
        timeFormat = temp1
        let month = ['JAN', 'FEB', 'MAR', 'APR', 'MAY', 'JUN', 'JUL', 'AUG', 'SEP', 'OCT', 'NOV', 'DEC']
        temp[1] = month[parseInt(temp[1]) - 1]
        newFormat = temp.join('-')
        if (newFormat.charAt(0) === '0') {
          newFormat = newFormat.slice(1)
        }
      }
      return newFormat + ' ' + timeFormat
    },
    getCurrency (currency) {
      if (currency === 'USD') {
        return '$'
      } else if (currency === 'INR') {
        return '₹'
      }
    }
  }
}