<template>
  <div class="m-accordion">
    <div class="ma__no-margin" >
      <div class="ma__text ma__text--left" :style="cssVars.headingProperty">
          {{heading}}
          <InfoTooltip v-if="infoTip" :title="infoTip"/>
      </div>
      <div class="ma__right">
        <div class="ma__line" :style="cssVars.lineProperty"></div>
        <div :style="cssVars.buttonProperty" :class="'ma__round-btn '+ (showState? '' : 'ma__round-btn--btn-padding') " v-on:click="changeState">
            {{showState == true ? '-':'+'}}
        </div>
      </div>
    </div>
    <transition name="slide">
      <div v-if="showState==true">
        <slot></slot>
      </div>
    </transition>
  </div>
</template>

<script>

export default {
  components: {
    InfoTooltip: () => import('@/components/ToolTip.vue')
  },
  data () {
    return {
      showState: false,
      accordionProperty: {}
    }
  },
  props: {
    showContent: {
      type: Boolean,
      default: false
    },
    heading: {
      type: String,
      default: 'Heading'
    },
    infoTip: {
      type: [String, Boolean],
      default: false
    },
    mAccordionProperty: {
      type: [Object, Array, String]
    }
  },
  methods: {
    changeState () {
      this.showState = !this.showState
      this.$emit('change-state', this.showState)
      return this.showState
    }
  },
  created () {
    this.accordionProperty = {
      buttonProperty: {
        background: '#DFE1E6',
        color: '#4E617B',
        border: '1px solid #b7b7b7',
        borderRadius: '999px',
        width: '18px',
        height: '18px'
      },
      lineProperty: {
        background: '#b7b7b7'
      },
      headingProperty: {
        color: '#4E617B'
      }
    }
  },
  mounted () {
    this.showState = this.showContent
    if (this.mAccordionProperty) {
      if (this.mAccordionProperty.buttonProperty && this.mAccordionProperty.buttonProperty.constructor === Object) {
        let buttonProp = {...this.accordionProperty.buttonProperty, ...this.mAccordionProperty.buttonProperty}
        this.accordionProperty.buttonProperty = buttonProp
      }
      if (this.mAccordionProperty.lineProperty && this.mAccordionProperty.lineProperty.constructor === Object) {
        let lineProp = {...this.accordionProperty.lineProperty, ...this.mAccordionProperty.lineProperty}
        this.accordionProperty.lineProperty = lineProp
      }
      if (this.mAccordionProperty.headingProperty && this.mAccordionProperty.headingProperty.constructor === Object) {
        let headingProp = {...this.accordionProperty.headingProperty, ...this.mAccordionProperty.headingProperty}
        this.accordionProperty.headingProperty = headingProp
      }
    }
  },
  computed: {
    cssVars () {
      return this.accordionProperty
    }
  }
}
</script>

<style scoped>
.ma__line{
  height: 1px;
  margin-top: 9px;
  max-width: 97%;
}
.ma__right {
  width: auto;
  overflow: hidden;
}
.ma__text--left{
  float: left;
  padding-right: 20px;
  color: inherit;
}
.ma__round-btn{
  border-radius: 999px;
  width: 18px;
  height: 18px;
  text-align: center;
  -webkit-transition: color 0.2s linear;
  transition: color 0.2s linear;
  line-height: 1;
  margin-right: 0;
  margin-top: -9px;
  cursor: pointer;
  float: right;
  display: flex;
  justify-content: center;
  align-items: center;
}
.slide-enter-active {
   -moz-transition-duration: 0.3s;
   -webkit-transition-duration: 0.3s;
   -o-transition-duration: 0.3s;
   transition-duration: 0.3s;
   -moz-transition-timing-function: ease-in;
   -webkit-transition-timing-function: ease-in;
   -o-transition-timing-function: ease-in;
   transition-timing-function: ease-in;
}

.slide-leave-active {
   -moz-transition-duration: 0.3s;
   -webkit-transition-duration: 0.3s;
   -o-transition-duration: 0.3s;
   transition-duration: 0.3s;
   -moz-transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
   -webkit-transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
   -o-transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
   transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
}

.slide-enter-to, .slide-leave {
   max-height: 100px;
   overflow: hidden;
}

.slide-enter, .slide-leave-to {
   overflow: hidden;
   max-height: 0;
}
</style>
