<template>
  <div id="mainMenu">
    <div id="sidemenu" :style="cssVars.defaultProperty">
      <ul class="ul_style">
        <router-link :to="item.route" v-for="(item, index) in icons" :key="item + index">
          <li class="li_style" :id="`svg-${index}`" :style="getStyle(index)" @mouseover="fillNewColor(index)" @mouseleave="removeColor(index)" @click="clickHandler(index)">
            <component :is="item.svg" :fill="getFillColor(index)"></component>
          </li>
        </router-link>
      </ul>
    </div>
    <div id="subMenu" @mouseleave="hideSubMenu()">
      <ul class='ul_list'>
        <li class="p-list" :id="index + 'listitems'" v-for="(item, index) in submenuArray" @mouseover="subHoverIndex=index" @mouseleave="subHoverIndex=null" :style="getSubStyle(mainMenu, index)" @click="clickSubHandler(mainMenu, index)" :key="item + index">
          <span v-if="((typeof item) !== 'object')">{{item}}</span>
          <span v-else>
            <router-link :to="item.route ? item.route : '#'">
              {{item.name }}
            </router-link>
          </span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>

export default {
  name: 'Sidebar',
  data () {
    return {
      sidemenuArray: [],
      submenuArray: [],
      mainMenu: 0,
      mainMenuActive: null,
      sidebarProperty: [],
      hoverIndex: null,
      clickIndex: null,
      subIndex: null,
      subHoverIndex: null
    }
  },
  props: {
    icons: {
      type: Array
    },
    mSidebarProperty: {
      type: [Array, Object, String]
    }
  },
  created () {
    this.sidebarProperty = {
      defaultProperty: {
        background: '#232323'
      },
      iconProperty: {
        fill: '#A1A1A1'
      },
      hoverIconProperty: {
        fill: '#fff'
      },
      selectedIconProperty: {
        background: '#383838',
        borderLeft: '4px solid #0978FC'
      },
      subMenuProperty: {
        background: '#383838',
        boxShadow: '1px 1px 1px 0 rgba(133,141,157,0.27)',
        fontSize: '14px',
        color: '#A1A1A1'
      },
      subMenuHoverProperty: {
        background: 'rgba(255,237,237,0.07)',
        color: '#fff'
      }
    }

    for (let i in this.icons) {
       if ( '/' + location.pathname.split('/')[1] == (this.icons[i].path )) {
         this.clickHandler(parseInt(i));
         break;
       }
    }
    
  },
  computed: {
    cssVars () {
      return this.sidebarProperty
    }
  },
  mounted () {
    if (this.mSidebarProperty) {
      if (this.mSidebarProperty.defaultProperty && this.mSidebarProperty.defaultProperty.constructor === Object) {
        let defaultProp = {...this.sidebarProperty.defaultProperty, ...this.mSidebarProperty.defaultProperty}
        this.sidebarProperty.defaultProperty = defaultProp
      }
      if (this.mSidebarProperty.iconProperty && this.mSidebarProperty.iconProperty.constructor === Object) {
        let iconProp = {...this.sidebarProperty.iconProperty, ...this.mSidebarProperty.iconProperty}
        this.sidebarProperty.iconProperty = iconProp
      }
      if (this.mSidebarProperty.hoverIconProperty && this.mSidebarProperty.hoverIconProperty.constructor === Object) {
        let hoverProp = {...this.sidebarProperty.hoverIconProperty, ...this.mSidebarProperty.hoverIconProperty}
        this.sidebarProperty.hoverIconProperty = hoverProp
      }
      if (this.mSidebarProperty.selectedIconProperty && this.mSidebarProperty.selectedIconProperty.constructor === Object) {
        let seHoverProp = {...this.sidebarProperty.selectedIconProperty, ...this.mSidebarProperty.selectedIconProperty}
        this.sidebarProperty.selectedIconProperty = seHoverProp
      }
      if (this.mSidebarProperty.subMenuProperty && this.mSidebarProperty.subMenuProperty.constructor === Object) {
        let subMenuProp = {...this.sidebarProperty.subMenuProperty, ...this.mSidebarProperty.subMenuProperty}
        this.sidebarProperty.subMenuProperty = subMenuProp
      }
      if (this.mSidebarProperty.subMenuHoverProperty && this.mSidebarProperty.subMenuHoverProperty.constructor === Object) {
        let subHoverProp = {...this.sidebarProperty.subMenuHoverProperty, ...this.mSidebarProperty.subMenuHoverProperty}
        this.sidebarProperty.subMenuHoverProperty = subHoverProp
      }
    }
  },
  methods: {
    getFillColor (index) {
      return this.icons[index].fillColor
    },
    getStyle(index) {
      let val = ''
      if (index === this.hoverIndex) {
        val = this.sidebarProperty.hoverIconProperty
      }
      if (index === this.hoverIndex && index === this.clickIndex || index === this.clickIndex) {
        val = this.sidebarProperty.selectedIconProperty
      }
      return val
    },
    fillNewColor (index) {
      this.icons[index].fillColor = this.sidebarProperty.hoverIconProperty.fill
      this.getFillColor(index)
      this.displaySubMenu('visible', index)
      this.hoverIndex = index
    },
    removeColor (index) {
      this.getFillColor(index)
      if (this.mainMenuActive !== index) {
        this.icons[index].fillColor = this.sidebarProperty.iconProperty.fill
        this.hoverIndex = null
      }
    },
    hideSubMenu () {
      this.displaySubMenu('vanish')
    },
    displaySubMenu (value, index) {
      let box = document.getElementById('subMenu')
      if (value === 'visible' && this.icons[index].childroute) {
        box.style.display = 'block'
        box.style.marginLeft = '30%'
        box.style.paddingTop = '16%'
        box.style.width = '211px'
        box.style.height = '864px'
        box.style.borderRadius = '0px'
        box.style.fontSize = this.sidebarProperty.subMenuProperty.fontSize
        box.style.color = this.sidebarProperty.subMenuProperty.color
        box.style.backgroundColor = this.sidebarProperty.subMenuProperty.background
        box.style.boxShadow = this.sidebarProperty.subMenuProperty.boxShadow
        this.submenuArray = []
        this.submenuArray = this.icons[index].childroute
        this.mainMenu = index
      } else if (value === 'vanish') {
        box.style.display = 'none'
      }
    },
    clickHandler (index) {
      this.removeAll()
      this.mainMenuActive = index
      this.fillNewColor(index)
      this.clickIndex = index
    },
    clickSubHandler (mainMenu, index) {
      this.clickHandler(mainMenu)
      this.subIndex = index
    },
    getSubStyle (mainMenu, index) {
      let val = ''
      let backProp = {
        background: this.sidebarProperty.subMenuProperty.background
      }
      if (this.subIndex === index && mainMenu === this.mainMenuActive) {
        if (this.subHoverIndex === index) {
          val = this.sidebarProperty.subMenuHoverProperty
        } else {
          val = {...this.sidebarProperty.subMenuHoverProperty, ...backProp}
        }
      } else if (this.subHoverIndex === index) {
        val = this.sidebarProperty.subMenuHoverProperty
      }
      return val
    },
    removeAll () {
      for (let i in this.icons) {
        this.icons[i].fillColor = this.sidebarProperty.iconProperty.fill
      }
      this.clickIndex = null
    }
  }
}
</script>

<style lang='scss' scoped>
  a:hover{
    text-decoration: none;
  }
  #mainMenu {
    height: 100%;
    width: 6%;
    z-index: 1;
    #sidemenu {
      box-shadow: 0 2px 3px 0 rgba(0,0,0,0.1);
      height: 100%;
      width: 100%;
      float: left;
      .ul_style {
        list-style-type: none;
        margin: 0px;
        padding: 0px;
        text-align: center;
        .li_style {
          padding-top: 15px;
          padding-bottom: 10px;
          margin: 32px 0;
          &:focus {
            outline: none;
          }
          > div {
            transition: transform .2s;
            &:hover {
              transform: scale(1.1);
            }
          }
          .p_style {
            margin: -3px;
            font-weight: 600;
            letter-spacing: -0.33px;
            line-height: 11px;
            padding-top: 6px;
          }
        }
      }
    }
    #subMenu {
      display: none;
      .ul_list {
        list-style-type: none;
        padding: 0px 0px 0px 70px;
        margin: 0px;
        text-align: left;
        .p-list {
          font-family: inherit;
          margin: 10px 20px 20px 0px;
          border-radius: 2px;
          span {
            width: 100%;
            height: 100%;
            display: block;
            a {
              padding: 5px 10px 5px 5px;
              color: inherit;
              width: 100%;
              height: 100%;
              display: block;
            }
          }
        }
      }
    }
  }
</style>
