  font-size: inherit;
  font-family: inherit;
  width: var(--width);
  position: relative;
  .md__input {
    display: flex;
    flex-direction: row;
    position: relative;
    input {
      padding: 0 5px;
      color: inherit;
      &:focus {
        outline: none;
      }
      &::placeholder {
        opacity: 0.5;
      }
    }
    .md__arrow {
      position: relative;
      right: 18px;
      cursor: pointer;
      display: flex;
      align-items: center;
      svg {
        fill: rgba(60,60,60,.5);
        transition: transform .15s cubic-bezier(1,-.115,.975,.855),-webkit-transform .15s cubic-bezier(1,-.115,.975,.855);
        transition-timing-function: cubic-bezier(1,-.115,.975,.855);
        &.arrow__up {
          transform: rotate(180deg) scale(1);
          path {
            fill: #0978FC;
          }
        }
      }
    }
  }
  .md__list {
    box-shadow: 0px 3px 6px 0px rgba(0, 0, 0, .15);
    width: inherit;
    cursor: pointer;
    overflow: auto;
    max-height: 170px;
    background: #fff;
    z-index: 1000;
    position: absolute;
    left: 0;
    &::-webkit-scrollbar {
      width: 5px;
    }
    &::-webkit-scrollbar-track {
      background : rgba(235, 235, 235, .3);
      border-radius: 2.5px;
      width: 5px;
    }
    &::-webkit-scrollbar-thumb {
      background : #EBEBEB;
      border-radius: 2.5px;
      width: 5px;
    }
    .md__list__item {
      padding: 0 5px;
      display: flex;
      align-items: center;
    }
  }
}
</style>
