<template>
    <div class="m-tab-init">
        <div class="m-tab-header">
            <div v-for="(tab, i) in tabs" :key="i" @click="selectTab(tab.name)" :class="tab.css">
                {{tab.name}}
            </div>
        </div>
        <div class="m-tab-body" :style="tabStyle">
            <div>
                <slot></slot>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
             tabs: []
        }
    },
    props: ['tabNames', 'tabStyle'],
    watch: {
        tabNames () {
            this.tabs = this.tabNames;
            this.initCss();
            this.effectChangeStyle(this.tabs[0].name);
        }
    },
    methods: {
        selectTab(name) {
            this.effectChangeStyle(name.trim())
            this.$emit("tabNameCB", name.trim())
        },
        effectChangeStyle(name) {
            for (let i in this.tabs) {
                if (name == this.tabs[i].name) {
                    this.tabs[i].css = 'active';
                } else {
                    this.tabs[i].css = {}
                }
            }
        },
        initCss() {
            let diffType = []
            for (let i in this.tabs) {
                diffType.push({name: this.tabs[i], css: {}})
            }
            this.tabs = diffType;
        }
    },
    mounted () {
        this.tabs = this.tabNames;
        this.initCss();
        this.effectChangeStyle(this.tabs[0].name);
    }
}
</script>

<style scoped lang="scss">
    .m-tab-init {
         padding: 10px;
         width: 100%;
        .m-tab-header {
            display: flex;
            div {
                margin-right: 5px;
                background: white;
                padding: 6px 46px;
                border-radius: 2px;
                justify-content: center;
                display: flex;
                height: 19px;
                text-transform: uppercase;
                line-height: 19px;
                text-align: center;
                cursor: pointer;
                border-radius: 2px;
            } 
            .active {
                height: 23px;
                margin-top: 2px;
                border-top-right-radius: 6px;
                border-top-left-radius: 6px;
                color: blue;
            } 
        }
        .m-tab-body {
            background: white;
            height: 90vh;
            border-radius: 5px;
        }
    }

</style>