<template>
      <div class="m-panel" @click="actToggleData($event)">
          <div class="m-panel-head" > 
              <div class="m-panel-head-left" slot="left-sidebar">
                   <slot name="left-sidebar"></slot>
                </div>
               <div class="m-panel-head-center">
                   <slot name="center-sidebar"></slot>
                </div> 
              <div class="m-panel-head-right">
                  <span><slot name="right-sidebar"></slot></span>
                   <span style="display: none;"><slot name="active-panel" ></slot> </span>
                   <span><slot name="deactive-panel"></slot></span>
              </div>
          </div>
        </div>
</template>

<script>
export default {
    methods: {
        actToggleData(e) {
            if (e.currentTarget.children[0].id == 'm-panel-active') {
                console.log(e.target.className)
                let counter = 0;
                if (e.target.className.includes('resetEllips')) {
                   counter = 1;
                } else  if (e.target.className.includes('m-panel-head-center')) {
                    counter = 2
                }

                if (counter > 0) {
                    return;
                }
             
                e.currentTarget.children[0].setAttribute("id", "");
                e.currentTarget.children[0].children[2].children[2].style.display = "block"
                e.currentTarget.children[0].children[2].children[1].style.display = "none"
                e.currentTarget.children[0].children[2].children[0].style.color = "black"
                return;
            } 
            for (let i of document.getElementsByClassName('m-panel-head')) {
                i.removeAttribute("id");
                i.children[1].children[1].classList.remove('resetEllips')
                i.children[2].children[2].style.display = "block"
                i.children[2].children[1].style.display = "none"
                i.children[2].children[0].style.color = "black"
            }
         
            e.currentTarget.children[0].setAttribute("id", 'm-panel-active');
            
            e.currentTarget.children[0].children[1].children[1].classList.add('resetEllips')
            e.currentTarget.children[0].children[2].children[1].style.display = "block"
            e.currentTarget.children[0].children[2].children[2].style.display = "none"
            e.currentTarget.children[0].children[2].children[0].style.color = "#0978FC"
        }
    }
}
</script>

<style scoped lang="scss">
    .m-panel {
        #m-panel-active {
            height: 201px;
            border-radius: 5px;
            background-color: #F8F8F8;
            box-shadow: 0 3px 8px 0 rgba(0,0,0,0.12);
        }
        .resetEllips {
            white-space: inherit !important; 
            width: auto !important; 
            overflow: none !important;
            text-overflow: ellipsis; 
        }
        .m-panel-head {
            height: 98px;
            border-radius: 5px;
            background-color: #FFFFFF;
            box-shadow: 0 2px 4px 0 rgba(0,0,0,0.12);
            display: flex;
            padding: 0px 36px;
            margin: 7px 0px;
            padding-top: 25px;
            cursor: pointer;
            .m-panel-head-left, .m-panel-head-center  {
                p:last-child {
                    padding-top: 8px;
                    opacity: 0.7;
                    color: #1E1E1E;

                }
            }
            .m-panel-head-center  {
                 p:last-child {
                    white-space: nowrap; 
                    width: 300px; 
                    overflow: hidden;
                    text-overflow: ellipsis; 
                 }
            }
            .m-panel-head-center {
                flex: 1 0 0;
                margin-left: 60px;
            }

            .m-panel-head-right {
                display: flex;
                span:first-child {
                    padding-right: 19px;
                }
            }
        }
    }
</style>