<template>
  <div :style="cssVars">
    <nav aria-label="breadcrumb">
        <ol class="breadcrumb" :style="cssVars.defaultProperty">
            <li v-for="(route,index) in  routenames" :key="index" :class="['breadcrumb-item', +(routenames.length-1 > 0 && routenames.length-1 == index)?'active':'']" :style="getStyle(routenames,index)" @mouseover="hoverIndex=index" @mouseleave="hoverIndex=null">
              <router-link v-if="route.route" :to="route.route">{{route.name}}</router-link>
              <span v-else>{{route.name}}</span>
            </li>
        </ol>
    </nav>
  </div>
</template>

<script>
export default {
  name: 'Breadcrumb',
  data () {
    return {
      breadcrumbProperty: {},
      hoverIndex: null
    }
  },
  props: {
    routenames: {
      type: Array
    },
    mBreadcrumbProperty: {
      type: [Object, String, Array],
    }
  },
  created () {
    this.breadcrumbProperty = {
      defaultProperty: {
        background: '#fff',
        color: '#303B4B',
        fontSize: '13px',
        height: '59px'
      },
      hoverProperty: {
        color: '#303B4B'
      },
      selectedProperty: {
        color: '#303B4B'
      }
    }
  },
  mounted () {
    if (this.mBreadcrumbProperty) {
      if (this.mBreadcrumbProperty.defaultProperty && this.mBreadcrumbProperty.defaultProperty.constructor === Object) {
        let defaultProp = {...this.breadcrumbProperty.defaultProperty, ...this.mBreadcrumbProperty.defaultProperty}
        this.breadcrumbProperty.defaultProperty = defaultProp
      }
      if (this.mBreadcrumbProperty.hoverProperty && this.mBreadcrumbProperty.hoverProperty.constructor === Object) {
        let hoverProp = {...this.breadcrumbProperty.hoverProperty, ...this.mBreadcrumbProperty.hoverProperty}
        this.breadcrumbProperty.hoverProperty = hoverProp
      }
      if (this.mBreadcrumbProperty.selectedProperty && this.mBreadcrumbProperty.selectedProperty.constructor === Object) {
        let selectedProp = {...this.breadcrumbProperty.selectedProperty, ...this.mBreadcrumbProperty.selectedProperty}
        this.breadcrumbProperty.selectedProperty = selectedProp
      }
    }
  },
  computed: {
    cssVars () {
      return this.breadcrumbProperty
    }
  },
  methods: {
    getStyle (routenames, index) {
      if ((routenames.length-1 > 0 && routenames.length-1 === index) || this.hoverIndex === index) {
        return this.breadcrumbProperty.selectedProperty
      } else if (this.hoverIndex === index) {
        return this.breadcrumbProperty.hoverProperty
      }
    }
  }
}
</script>

<style lang='scss' scoped>
  .breadcrumb {
    margin-left: 0;
    margin-bottom: 0px;
    border-radius: 0px;
    padding: 0 25px;
    font-weight: 500;
    display: flex;
    align-items: center;
    list-style: none;
    line-height: 18px;
    box-shadow: 0 1px 3px 0 rgba(0,0,0,0.2);
    .breadcrumb-item {
      display: inline-block;
      opacity: 0.5;
      + .breadcrumb-item {
        &:before {
          padding: 0 5px;
          color: currentColor;
          content: '/';
        }
      }
      &:hover {
        opacity: 1;
      }
      &:last-child {
        opacity: 1;
      }
    }
  }
  .breadcrumb-item a, .breadcrumb-item a:hover {
    text-decoration: none;
    color: inherit;
  }
</style>
