<template>
  <label class="m__label" @mouseover="hovering=true" @mouseleave="hovering=false" :style="cssVars.defaultProperty">
    <input type="radio" v-model="checkRadio" class="m__radio" @click="triggerClick()" :disabled="disabled" :value="radioValue"/>
    <span class="checkmark" :style="getRadioStyle" @click="stopProp()"></span>
    <span v-if="radioLabel" class="m__radio__label" :style="getLabelStyle"> {{radioLabel}} </span>
  </label>
</template>

<script>
export default {
  name: 'Radio',
  data () {
    return {
      checkRadio: false,
      radioProperty: {},
      hovering: false,
      disabled: false
    }
  },
  props: {
    radioChecked: {
      type: [String, Boolean, Number],
      default: false
    },
    radioValue: {
      type: [String, Boolean, Number],
      default: false
    },
    radioLabel: {
      type: [String, Boolean, Number],
      default: false
    },
    selectDisabled: {
      type: Boolean,
      default: false
    },
    mRadioProperty: {
      type: [String, Object, Array]
    }
  },
  created () {
    this.radioProperty = {
      defaultProperty: {
        display: 'flex'
      },
      radiobtnProperty: {
        height: '16px',
        width: '16px',
        backgroundColor: '#fff',
        border: '2px solid #D0D3D7',
        color: '#fff'
      },
      hoverProperty: {
        border: '2px solid #0978FC',
        color: '#fff'
      },
      selectedProperty: {
        border: '2px solid #0978FC',
        color: '#0978FC'
      },
      disabledProperty: {
        border: '2px solid #CBCED1',
        color: '#CBCED1',
        cursor: 'default'
      },
      labelProperty: {
        color: '#1F2631',
        left: '14px'
      },
      labelHoverProperty: {
        color: '#1F2631'
      },
      labelSelectedProperty: {
        color: '#1F2631'
      }
    }
  },
  computed: {
    cssVars () {
      return this.radioProperty
    },
    getRadioStyle () {
      let val = ''
      if (typeof this.checkRadio === 'boolean') {
        if (this.checkRadio) {
          if (this.selectDisabled) {
            val = {...this.radioProperty.radiobtnProperty, ...this.radioProperty.disabledProperty}
          } else {
            if (this.hovering) {
              val = {...this.radioProperty.radiobtnProperty, ...this.radioProperty.selectedProperty}
            } else {
              val = {...this.radioProperty.radiobtnProperty, ...this.radioProperty.selectedProperty}
            }
          }
        } else {
          if (this.hovering) {
            val = {...this.radioProperty.radiobtnProperty, ...this.radioProperty.hoverProperty}
          } else {
            val = this.radioProperty.radiobtnProperty
          }
        }
      } else if (typeof this.radioValue === 'string' || typeof this.radioValue === 'number') {
        if (this.checkRadio === this.radioValue) {
          if (this.hovering) {
            val = {...this.radioProperty.radiobtnProperty, ...this.radioProperty.selectedProperty}
          } else {
            val = {...this.radioProperty.radiobtnProperty, ...this.radioProperty.selectedProperty}
          }
        } else {
          if (this.hovering) {
            val = {...this.radioProperty.radiobtnProperty, ...this.radioProperty.hoverProperty}
          } else {
            val = this.radioProperty.radiobtnProperty
          }
        }
      }
      return val
    },
    getLabelStyle () {
      let val = ''
      if (typeof this.checkRadio === 'boolean') {
        if (this.checkRadio) {
          if (this.selectDisabled) {
            let disabledProp = {
              color: this.radioProperty.disabledProperty.color
            }
            val = {...this.radioProperty.labelProperty, ...disabledProp}
          } else {
            if (this.hovering) {
              val = {...this.radioProperty.labelProperty, ...this.radioProperty.labelSelectedProperty}
            } else {
              val = {...this.radioProperty.labelProperty, ...this.radioProperty.labelSelectedProperty}
            }
          }
        } else {
          if (this.hovering) {
            val = {...this.radioProperty.labelProperty, ...this.radioProperty.labelHoverProperty}
          } else {
            val = this.radioProperty.labelProperty
          }
        }
      } else if (typeof this.radioValue === 'string' || typeof this.radioValue === 'number') {
        if (this.checkRadio === this.radioValue) {
          if (this.hovering) {
            val = {...this.radioProperty.labelProperty, ...this.radioProperty.labelSelectedProperty}
          } else {
            val = {...this.radioProperty.labelProperty, ...this.radioProperty.labelSelectedProperty}
          }
        } else {
          if (this.hovering) {
            val = {...this.radioProperty.labelProperty, ...this.radioProperty.labelHoverProperty}
          } else {
            val = this.radioProperty.labelProperty
          }
        }
      }
      return val
    }
  },
  mounted () {
    this.checkRadio = this.radioChecked
    if (this.selectDisabled) {
      this.checkRadio = true
      this.disabled = true
    }
    if (this.mRadioProperty) {
      if (this.mRadioProperty.defaultProperty && this.mRadioProperty.defaultProperty.constructor === Object) {
        let defaultProp = {...this.radioProperty.defaultProperty, ...this.mRadioProperty.defaultProperty}
        this.radioProperty.defaultProperty = defaultProp
      }
      if (this.mRadioProperty.hoverProperty && this.mRadioProperty.hoverProperty.constructor === Object) {
        let hoverProp = {...this.radioProperty.hoverProperty, ...this.mRadioProperty.hoverProperty}
        this.radioProperty.hoverProperty = hoverProp
      }
      if (this.mRadioProperty.radiobtnProperty && this.mRadioProperty.radiobtnProperty.constructor === Object) {
        let radioProp = {...this.radioProperty.radiobtnProperty, ...this.mRadioProperty.radiobtnProperty}
        this.radioProperty.radiobtnProperty = radioProp
      }
      if (this.mRadioProperty.selectedProperty && this.mRadioProperty.selectedProperty.constructor === Object) {
        let selectProp = {...this.radioProperty.selectedProperty, ...this.mRadioProperty.selectedProperty}
        this.radioProperty.selectedProperty = selectProp
      }
      if (this.mRadioProperty.disabledProperty && this.mRadioProperty.disabledProperty.constructor === Object) {
        let disabledProp = {...this.radioProperty.disabledProperty, ...this.mRadioProperty.disabledProperty}
        this.radioProperty.disabledProperty = disabledProp
      }
      if (this.mRadioProperty.labelProperty && this.mRadioProperty.labelProperty.constructor === Object) {
        let labelProp = {...this.radioProperty.labelProperty, ...this.mRadioProperty.labelProperty}
        this.radioProperty.labelProperty = labelProp
      }
      if (this.mRadioProperty.labelHoverProperty && this.mRadioProperty.labelHoverProperty.constructor === Object) {
        let labelHoverProp = {...this.radioProperty.labelHoverProperty, ...this.mRadioProperty.labelHoverProperty}
        this.radioProperty.labelHoverProperty = labelHoverProp
      }
      if (this.mRadioProperty.labelSelectedProperty && this.mRadioProperty.labelSelectedProperty.constructor === Object) {
        let labelSelectProp = {...this.radioProperty.labelSelectedProperty, ...this.mRadioProperty.labelSelectedProperty}
        this.radioProperty.labelSelectedProperty = labelSelectProp
      }
    }
  },
  methods: {
    triggerClick () {
      if (!this.selectDisabled) {
        if (typeof this.checkRadio === 'boolean') {
          this.checkRadio = !this.checkRadio
          this.$emit('on-radio-checked', this.checkRadio)
        } else if (typeof this.radioValue === 'string' || typeof this.radioValue === 'number') {
          this.checkRadio = this.radioValue
          this.$emit('on-radio-checked', this.checkRadio)
        }
      }
    },
    stopProp() {
      event.stopPropagation()
    }
  }
}
</script>

<style scoped lang="scss">
  .m__label {
    display: flex;
    position: relative;
    .m__radio__label {
      position: relative;
    }
    .m__radio {
      cursor: pointer;
      position: relative;
      opacity: 0;
    }
    .checkmark {
      position: absolute;
      cursor: pointer;
      display: block;
      top: 50%;
      transform: translateY(-50%);
      border-radius: 100%;
      z-index: 5;
      transition: border .25s linear;
      -webkit-transition: border .25s linear;
      &:before {
        display: block;
        position: absolute;
        content: '';
        border-radius: 100%;
        background: currentColor;
        height: 8px;
        width: 8px;
        top: 50%;
        transform: translate(-50%, -50%);
        left: 50%;
        margin: auto;
        transition: background 0.25s linear;
        -webkit-transition: background 0.25s linear;
      }
    }
  }
  .radio-wrap {
    .m__label {
      height: 16px;
    }
  }
</style>
