<template>
  <div :class="['toggle-button', getClass()]"  @click="actChilda()">
    <span v-if="leftLabel" :class="'toggle-text left ' + getLabeActive ('left')" :style="leftLabelVars">{{leftLabel}}</span>
      <span class="switch" :style="cssVars.defaultProperty">
        <input
          class="toggle-element"
          type="checkbox"
          v-model="toggleCheckBox"
          :disabled="disabled"
        />
        <span class="slider round" :style="checkVars"></span>
      </span>
    <span v-if="rightLabel" @click="actChilda()" :class="'toggle-text right ' + getLabeActive ('right')" :style="rightLabelVars">{{rightLabel}}</span>
  </div>
</template>
<script>
export default{
  name: 'Toggle',
  data () {
    return {
      toggleProperty: {}
    }
  },
  props: {
    toggleCheckBox: {
      type: Boolean,
      default: true
    },
    leftLabel: {
      type: [Boolean, String],
      default: false
    },
    rightLabel: {
      type: [Boolean, String],
      default: false
    },
    mToggleProperty: {
      type: [Object, Array, String]
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    cssVars () {
      return this.toggleProperty
    },
    checkVars () {
      let val= ''
      if (!this.disabled) {
        if (this.toggleCheckBox) {
          let borderProp = {
            '--checked-border': this.toggleProperty.sliderCheckedProperty.background
          }
          val = {...this.toggleProperty.sliderCheckedProperty, ...borderProp}
        } else {
          let borderProp = {
            '--checked-border': this.toggleProperty.sliderProperty.background
          }
          val = {...this.toggleProperty.sliderProperty, ...borderProp}
        }
      } else if (this.disabled) {
        let borderProp = {
          '--checked-border': '#CBCED1',
          background: '#CBCED1',
          color: '#CBCED1',
          cursor: 'default'
        }
        val = {...this.toggleProperty.sliderProperty, ...borderProp}
      }
      return val
    },
    leftLabelVars () {
      let val= ''
      if(this.leftLabel && this.rightLabel) {
        if (this.toggleCheckBox) {
          let cssProp = {
            color: '#CBCED1'
          }
          val = {...this.toggleProperty.labelProperty, ...cssProp}
        } else {
          let cssProp = {
            color: this.toggleProperty.sliderProperty.background
          }
          val = {...this.toggleProperty.labelProperty, ...cssProp}
        }
      } else if (this.leftLabel) {
        if (!this.disabled) {
          if (this.toggleCheckBox) {
            let cssProp = {
              color: this.toggleProperty.sliderCheckedProperty.background
            }
            val = {...this.toggleProperty.labelProperty, ...cssProp}
          } else {
            let cssProp = {
              color: this.toggleProperty.sliderProperty.background
            }
            val = {...this.toggleProperty.labelProperty, ...cssProp}
          }
        } else if (this.disabled) {
          let cssProp = {
            color: '#CBCED1'
          }
          val = {...this.toggleProperty.labelProperty, ...cssProp}
        }
      }
      return val
    },
    rightLabelVars () {
      let val= ''
      if (this.leftLabel && this.rightLabel) {
        if (!this.toggleCheckBox) {
          let cssProp = {
            color: '#CBCED1'
          }
          val = {...this.toggleProperty.labelProperty, ...cssProp}
        } else {
          let cssProp = {
            color: this.toggleProperty.sliderCheckedProperty.background
          }
          val = {...this.toggleProperty.labelProperty, ...cssProp}
        }
      }
      return val
    }
  },
  created () {
    this.toggleProperty = {
      defaultProperty: {
        height: '8px',
        width: '16px'
      },
      sliderProperty: {
        background: '#FF9696',
        color: '#fff'
      },
      sliderCheckedProperty: {
        background: '#0978FC',
        color: '#fff',
        boxShadow: '0 0 1px #0978FC',
      },
      labelProperty: {
        fontSize: '1vw',
        lineHeight: '1.2vw',
        fontWeight: '500'
      }
    }
  },
  mounted () {
    if (this.mToggleProperty) {
      if (this.mToggleProperty.defaultProperty && this.mToggleProperty.defaultProperty.constructor === Object) {
        let defaultProp = {...this.toggleProperty.defaultProperty, ...this.mToggleProperty.defaultProperty}
        this.toggleProperty.defaultProperty = defaultProp
      }
      if (this.mToggleProperty.sliderProperty && this.mToggleProperty.sliderProperty.constructor === Object) {
        let sliderProp = {...this.toggleProperty.sliderProperty, ...this.mToggleProperty.sliderProperty}
        this.toggleProperty.sliderProperty = sliderProp
      }
      if (this.mToggleProperty.sliderCheckedProperty && this.mToggleProperty.sliderCheckedProperty.constructor === Object) {
        let defaultProp = {...this.toggleProperty.sliderCheckedProperty, ...this.mToggleProperty.sliderCheckedProperty}
        this.toggleProperty.defaultProperty = defaultProp
      }
      if (this.mToggleProperty.labelProperty && this.mToggleProperty.labelProperty.constructor === Object) {
        let labelProp = {...this.toggleProperty.labelProperty, ...this.mToggleProperty.labelProperty}
        this.toggleProperty.labelProperty = labelProp
      }
    }
    if (this.disabled) {
      this.toggleCheckBox = false
    }
  },
  methods: {
    actChilda () {
      if (!this.disabled) {
        this.$emit('changetoggle', this.toggleCheckBox)
      }
    },
    getLabeActive (side) {
      let cssClass = ''
      if (this.toggleCheckBox && side === 'right') {
        cssClass = 'active'
      }
      if (!this.toggleCheckBox && side === 'left') {
        cssClass = 'active'
      }
      return cssClass
    },
    getClass () {
      let val = ''
      if (this.leftLabel && this.rightLabel) {
        val = 'toggle-both'
      } else if (this.leftLabel) {
        val = 'toggle-left'
      }
      return val
    }
  }
}
</script>
<style scoped lang='scss'>
  .switch {
    position: relative;
    display: inline-block;
    height: 12px;
    width: 21px;   
  }
  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
    position: relative;
    z-index: 1;
    cursor: pointer;
  }
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    -webkit-transition: .4s;
    transition: .4s;
  }
  .slider:before {
    position: absolute;
    content: "";
    height: 12px;
    width: 12px;
    left: -2px;
    bottom: -2px;
    background-color: currentColor;
    -webkit-transition: .4s;
    transition: .4s;
    box-shadow: 0 0 2px 0 rgba(0,0,0,0.44);
    border: .5px solid var(--checked-border);
  }
  .toggle-element:checked + .slider:before {
    -webkit-transform: translateX(9px);
    -ms-transform: translateX(9px);
    transform: translateX(9px);
  }
  .slider.round {
    border-radius: 7.5px;
  }
  .slider.round:before {
    border-radius: 50%;
  }
  .toggle-button {
    display: flex;
    justify-content: space-between;
    align-items: center;
    &.toggle-left {
      width: 95px;
    }
    &.toggle-both {
      width: 140px;
    }
  }
</style>
