<script>
  export default {
    name:'Header',
    data() {
      return {
        dropdownLogo: require('@/assets/dropdown.svg'),
        showDropdown: false
      }
    },
    props: {
      indexHead: {
        type: Boolean,
        default: true
      },
      headerContent: {
        type: [Object, Array]
      }
    },
    methods: {
      openPopup () {
        this.showDropdown = !this.showDropdown               
      },
      editProfile () {
        this.$emit('editProfile')
        this.showDropdown = !this.showDropdown
      },
      logout () {
        this.$emit('logout')
        this.showDropdown = !this.showDropdown
      },
      getEditStyle () {
        let val = ''
        if (this.indexHead) {
          val = {visibility: 'visible'}
        } else {
          val = {visibility: 'hidden'}
        }
        return val
      }
    }    
  }
</script>

