<template>
  <div class="m-control" :style="{width: cssVars.width}">
    <textarea 
    class="m-textboxarea" 
    :placeholder="placeholderText" 
    v-model="taInput"
    @mouseover="showHover" 
    @mouseleave="showLeave" 
    v-on:focus="setFocus(true)" 
    v-on:blur="setFocus(false)"
    :maxlength="mLength"
    :style="cssVars" @keyup="textCounter" :disabled="disabled">
    </textarea>
    <p v-show="maxlength" :style="getStyle(remaining)"> {{ remaining }} Characters Remaining </p>
  </div>
</template>

<script>

export default {
  name: 'Textarea',
  data () {
    return {
      textareaProperty: {},
      taInput: '',
      placeholderText: '',
      hovering: false,
      focus: false,
      remaining: ''
    }
  },
  props: {
    placeholder: {
      type: String,
      default: ''
    },
    textareaInput: {
      type: String,
      default: ''
    },
    mTextareaProperty: {
      type: [Object, Array, String]
    },
    maxlength: {
      type: [Boolean, Number],
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    },
    resetTextarea: {
      type: [Boolean, String, Number],
      default: false
    }
  },
  watch: {
    taInput () {
      console.log(this.taInput)
      this.$emit('on-change-input', this.taInput)
    },
    textareaInput (val) {
      this.taInput = val
    },
    maxlength () {
      this.mLength = this.maxlength
      if (this.maxlength) {
        this.remaining = this.maxlength - this.taInput.length
      }
    },
    resetTextarea () {
      console.log(this.resetTextarea)
      if (this.resetTextarea !== false) {
        this.taInput = this.resetTextarea
      } if (this.resetTextarea === true) {
        this.taInput = ''
        this.remaining = this.maxlength
      }
    }
  },
  methods: {
    showHover () {
      this.hovering = !this.hovering
    },
    showLeave () {
      this.hovering = !this.hovering
    },
    setFocus (val) {
      this.focused = val
    },
    textCounter () {
      if (this.maxlength) {
        this.remaining = this.maxlength - this.taInput.length
      }
    },
    getStyle (remaining) {
      return remaining? {'color': '#9BA0AD'} : {'color': '#c54444'}
    }
  },
  created () {
    this.taInput = this.textareaInput
    this.mLength = this.maxlength
    this.textareaProperty = {
      defaultProperty: {
        background: '#fff',
        border: '1px solid #A9A9A9',
        color: '#A9A9A9',
        fontSize: '12px',
        width: '225px'
      },
      hoverProperty: {
        border: '1px solid #409aff'
      },
      focusProperty: {
        border: 1 + 'px solid #409aff',
        boxShadow: '0 0 0 0.2rem rgba(64,154,255,.15)'
      }
    }
  },
  mounted () {
    this.placeholderText = this.placeholder
    if (this.mTextareaProperty && this.mTextareaProperty.defaultProperty && this.mTextareaProperty.defaultProperty.constructor === Object) {
      let defaultProp = {...this.textareaProperty.defaultProperty, ...this.mTextareaProperty.defaultProperty}
      this.textareaProperty.defaultProperty = defaultProp
    }
    if (this.mTextareaProperty && this.mTextareaProperty.hoverProperty && this.mTextareaProperty.hoverProperty.constructor === Object) {
      let hoverProp = {...this.textareaProperty.hoverProperty, ...this.mTextareaProperty.hoverProperty}
      this.textareaProperty.hoverProperty = hoverProp
    }
    if (this.mTextareaProperty && this.mTextareaProperty.focusProperty && this.mTextareaProperty.focusProperty.constructor === Object) {
      let focusProp = {...this.textareaProperty.focusProperty, ...this.mTextareaProperty.focusProperty}
      this.textareaProperty.focusProperty = focusProp
    }
    this.remaining = this.maxlength
    if (this.maxlength) {
      this.remaining = this.maxlength - this.taInput.length
    }
  },
  computed: {
    cssVars () {
      let val = ''
      if (this.hovering) {
        val = {...this.textareaProperty.defaultProperty, ...this.textareaProperty.hoverProperty}
      } else if (this.focused) {
        val = {...this.textareaProperty.defaultProperty, ...this.textareaProperty.focusProperty}
      } else {
        val = this.textareaProperty.defaultProperty
      }
      return val
    }
  }
}
</script>

<style scoped lang="scss">
  .m-textboxarea {
    box-sizing: border-box;
    background-clip: padding-box;
    border-radius: 2px;
    border-width: 1px;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out,-webkit-box-shadow .15s ease-in-out;
    padding: 5px;
    height: inherit;
    width: 100%;
    resize: none;
    font-family: inherit;
    display: block;
    margin: 0;
    &:focus {
      outline: none;
    }
    &::placeholder {
      opacity: 0.5;
    }
  }
  .m-control {
    width: inherit;
    height: inherit;
    p {
      color: #9BA0AD;
      font-size: 10px;
      letter-spacing: 0;
      line-height: 13px;
      font-family: inherit;
      position: relative;
      float: right;
    }
  }
</style>
