<template>
<div class="logins-divs">
        <div class="cards-headers">
            <div class="cards-titles">
                <div class="card-div">
                    <div v-if="imgLogo" class="cards-logo">
                      <img class="vliveimg" :src="imgLogo">
                    </div>                    
                    <div v-else class="cards-logo">
                      <p>Insert Logo here</p> 
                    </div>
                    <div class="rectangles"></div>
                    <div v-if="title" class="cards-name">
                      <p class="adminname">{{title}}</p>
                    </div>
                    <div v-else class="cards-name">
                      <p class="adminname">Insert Title Here</p>
                    </div>
                </div>
            </div>
        </div>
        <form id='loginform' class="formslogin">
            <div v-if="withAdminID" class="forms-group-bottom forms-group">
                <input id="adminid"  type="text" v-model="adminid" class="forms-control" placeholder="Admin Id" maxlength="64">
                <div class="message">{{ validation.firstError('adminid') }}</div>                               
            </div>
            <div v-else class="forms-group-bottom forms-group">
                <input id="email"  type="text" v-model="email" class="forms-control" placeholder="Admin Id" maxlength="64">
                <div class="message">{{ validation.firstError('email') }}</div>                
            </div>

            <div class="forms-group">
                <input id="password"  type="password" v-model="password" class="forms-control" placeholder="Password">
                <div class="message">{{ validation.firstError('password') }}</div>
            </div>
            <div v-if="withPassPhrase" class="forms-group">
                <input id="passphrase"  type="password" v-model="passphrase" class="forms-control" placeholder="Passphrase">
                <div class="message">{{ validation.firstError('passphrase') }}</div>
            </div>
            <div v-else class="forms-group extra">
                <!-- <input id="passphrase"  type="password" v-model="passphrase" class="forms-control" placeholder="Passphrase">
                <div class="message">{{ validation.firstError('passphrase') }}</div> -->
            </div>
            <div class="login-btn">
              <button ref="disableButton" type="submit" class="login" @click.prevent="submit">LOGIN</button>
            </div>
        </form>
</div>
</template>

<script>
import Vue from 'vue'
import SimpleVueValidation from 'simple-vue-validator'
const Validator = SimpleVueValidation.Validator
Vue.use(SimpleVueValidation)
export default {
  name:'Login',
  data(){
    return {
          email: null,
          password: null,
          passphrase: null,
          loginError: null, 
          adminid: null,               
        }
    },
    props:{
        imgLogo:{
          type: String
        },
        title: {
          type: String
        },
        withPassPhrase:{
          type: Boolean,
          default: true
        },
        withAdminID : {
          type: Boolean,
          default: false
        }
    },
    validators: { 
        adminid : function (value) {
          if(this.withAdminID){
            return Validator.value(value).required('Admin ID Required')
          }          
        },
        email: function (value) {
          if(!this.withAdminID) {
            return Validator.value(value).required('Admin ID Required').email().maxLength(64)
          }
        },
        password: function (value) {
            return Validator.value(value).required('Password required').maxLength(15)
        },
        passphrase: function (value) {
          if(this.withPassPhrase){
             return Validator.value(value).required('Passphrase required')
          }
         
        }

    },
    methods: {
      submit () {
        console.log('here')
        let self = this
          this.$validate()
            .then(function (success){
             if(success){
               console.log('here')
               self.$emit("Submit", {email:self.email,adminid: self.adminid, password:self.password, passphrase:self.passphrase})
               console.log('emitted')
            }
            })
        }
    }
}       

</script>

