            <span v-if="getNoSelected === 0" class="placeholder">{{ placeholderText }}</span>
            <span v-if="getNoSelected > 2" class="extra-col" v-show="!showCont" @click="showContAll()">{{ getNoSelected - 2 }} more</span>
