      <div class="md__list__div">
        <div v-for="item in itemList" :key="item.name">
          <li @click="selectItem(item)" v-show="itemVisible(item)" :class="['md__list__item', getClassItem(item)]">
            {{ item.name }}
            <div class="check"></div>
          </li>
        </div>
        <span v-show="!getVisibleItem" class="md__list__item">Sorry, no matching options</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'SelectDropdown',
  data () {
    return {
      inputValue: '',
      showList: false,
      selectedItem: [],
      resetItem: {},
      selectProperty: {},
      placeholderText: '',
      showCont: false,
    }
  },
  props: {
    itemList: {
      type: Array,
      default: () => [
        { name: 'Content1', id: 1 },
        { name: 'List2', id: 2 },
        { name: 'Item3', id: 3 },
        { name: 'Content4', id: 4 },
        { name: 'Content5', id: 5 },
        { name: 'Content6', id: 6 }
      ]
    },
    placeholder: {
      type: String,
      default: () => ''
    },
    selected: {
      type: [Number, Array],
      default: null
    },
    mSelectProperty: {
      type: Object,
    },
    resetDropdown: {
      type: [Object, Number, String, Boolean],
      default: false
    }
  },
  computed: {
    cssVars () {
      return {
        '--bg-color': this.selectProperty.bgColor,
        '--text-color': this.selectProperty.textColor,
        '--border-color': this.selectProperty.borderColor,
        '--width': this.selectProperty.width + 'px',
        '--height': this.selectProperty.height + 'px',
        '--border-radius': this.selectProperty.borderRadius + 'px',
        '--dropdown-width': +this.selectProperty.width + 'px',
        '--select-bg-color': this.selectProperty.selectBgColor,
        '--unselected-bg-color': this.selectProperty.unselectedBgColor
      }
    },
    getVisibleItem () {
      let val = false
      if (this.inputValue !== '') {
        for (let item in this.itemList) {
          let currentName = this.itemList[item].name.toLowerCase()
          let currentInput = this.inputValue.toLowerCase()
          let myPattern = new RegExp('(\\w*'+currentInput+'\\w*)','gi')
          let matches = currentName.match(myPattern)
          if (matches) {
            val = true
          }
        }
      } else {
        val = true
      }
      return val
    },
    getSelectedName () {
      let val = ''
      if (this.selectedItem.name) {
        val = this.selectedItem.name
      } else if (this.resetItem.name) {
        val = this.resetItem.name
      }
      return val
    },
    getLength () {
      return Object.keys(this.selectedItem).length === 0
    },
    getNoSelected () {
      return this.selectedItem.length
    }
  },
  watch: {
    inputValue () {
      this.getStyle()
      this.getVisibleItem
    },
    resetDropdown () {
      if (this.resetDropdown || this.resetDropdown === 0) {
        if (!Array.isArray(this.resetDropdown)) {
          console.log(this.itemList.find(i => i.id === this.resetDropdown))
          if (this.itemList.find(i => i.id === this.resetDropdown)) {
            this.selectItem(this.itemList.find(i => i.id === this.resetDropdown))
          } else {
            this.$emit('on-item-selected', [])
            this.selectedItem = []
            this.placeholderText = this.placeholder
          }
        } else if (Array.isArray(this.resetDropdown)) {
          for (let j in this.resetDropdown) {
            this.selectItem(this.itemList.find(i => i.id === this.resetDropdown[j]))
          }
        }
      }
    }
  },
  created () {
    this.selectProperty = {
      textColor:'#576175',
      bgColor: '#fff',
      borderColor: '#D4DAE4',
      height: '38',
      width: '259',
      borderRadius: '2',
      selectBgColor: '#0978FC',
      unselectedBgColor: '#fff'
    }
    this.placeholderText = this.placeholder
    if (this.selected) {
      if (typeof this.selected === 'number' || typeof this.resetDropdown === 'string') {
        this.selectItem(this.itemList.find(i => i.id === this.selected))
      } else if (Array.isArray(this.selected)) {
        for (let j in this.selected) {
          this.selectItem(this.itemList.find(i => i.id === this.selected[j]))
        }
      }
    }
  },
  mounted () {
    this.selectProperty = {...this.selectProperty, ...this.mSelectProperty}
    document.addEventListener('click', this.close)
  },
  methods: {
    itemVisible (item) {
      if (this.inputValue === '') {
        return true
      } else {
        let currentName = item.name.toLowerCase()
        let currentInput = this.inputValue.toLowerCase()
        return currentName.includes(currentInput)
      }
    },
    listStyles () {
      return {
        margin: '3px 5px'
      }
    },
    showContAll () {
      event.stopPropagation()
      this.showCont = true
    },
    getIndex(theItem) {
      let index = this.selectedItem.findIndex(e1 => e1.id === theItem.id)
      if (index > 1 && this.showCont === false) {
        return false
      } else if (index <= 1) {
        return true
      } else if (this.showCont === true) {
        return true
      }
    },
    getClassItem (theItem) {
      let found = this.selectedItem.some(e1 => e1.id === theItem.id)
      if (found) {
        return 'selected'
      } else {
        return ''
      }
    },
    getStyle () {
      let val = ''
      if (this.inputValue.length) {
        val = {
          backgroundImage: 'none',
          textIndent: 0
        }
      }
      return val
    },
    showDrop (){
      this.showClick()
      this.stopClose()
    },
    selectItem (theItem) {
      let found = this.selectedItem.some(e1 => e1.id === theItem.id)
      if (!found) {
        this.selectedItem.push(theItem)
      } else {
        this.resetSelection(theItem)
      }
      if (this.getNoSelected <= 2) {
        this.showCont = false
      }
      this.inputValue = ''
      this.$emit('on-item-selected', this.selectedItem)
    },
    resetSelection (theItem) {
      event.stopPropagation()
      let index = this.selectedItem.findIndex(e1 => e1.id === theItem.id)
      if (index === 0 || index) {
        this.selectedItem.splice(index, 1)
      }
      if (this.getNoSelected <= 2) {
        this.showCont = false
      }
      this.resetItem = this.selectedItem
      this.$nextTick( () => this.$refs.dropdowninput.focus() )
      this.$emit('on-item-reset', this.resetItem)
    },
    stopClose () {
      event.stopPropagation()
    },
    showClick () {
      this.showList = !this.showList
    },
    close (e) {
      if (!this.$el.contains(e.target)) {
        this.showList = false
      }
    }
  }
}
</script>

