<template>
  <input
  :disabled="disabled"
  :placeholder="placeholderText"
  ref="textinput"
  :maxlength="maxlength"
  :minlength="minlength"
  :type="type"
  :required="required"
  class="m-textbox"
  :style="cssVars" v-model="tbInput" @mouseover="showHover" @mouseleave="showLeave" v-on:focus="setFocus(true)" v-on:blur="setFocus(false)">
</template>

<script>

export default {
  name: 'Textbox',
  data () {
    return {
      placeholderText: '',
      inputProperty: {},
      tbInput: '',
      hovering: false,
      focused: false
    }
  },
  props: {
    placeholder: {
      type: String,
      default: () => ''
    },
    mInputProperty: {
      type: [Object, Array, String]
    },
    textboxInput: {
      type: [String, Number],
      default: ''
    },
    disabled: {
      type: Boolean,
      default: false
    },
    maxlength: {
      type: [Boolean, Number],
      default: false
    },
    minlength: {
      type: [Boolean, Number],
      default: false
    },
    type: {
      type: [Boolean, String],
      default: 'text'
    },
    required: {
      type: Boolean,
      default: false
    },
    resetText: {
      type: [Boolean, String, Number],
      default: false
    }
  },
  watch: {
    tbInput () {
      this.$emit('on-change-value', this.tbInput)
    },
    textboxInput (cb) {
      this.tbInput = cb;
    },
    resetText () {
      console.log(this.resetText)
      if (this.resetText !== false) {
        this.tbInput = this.resetText
      } if (this.resetText === true) {
        this.tbInput = ''
      }
    }
  },
  methods: {
    showHover () {
      this.hovering = !this.hovering
    },
    showLeave () {
      this.hovering = !this.hovering
    },
    setFocus (val) {
      this.focused = val
    }
  },
  computed: {
    cssVars () {
      let val = ''
      if (this.hovering) {
        val = {...this.inputProperty.defaultProperty, ...this.inputProperty.hoverProperty}
      } else if (this.focused) {
        val = {...this.inputProperty.defaultProperty, ...this.inputProperty.focusProperty}
      } else {
        val = this.inputProperty.defaultProperty
      }
      return val
    }
  },
  created () {
    this.inputProperty = {
      defaultProperty: {
        background: '#fff',
        border: 1 + 'px solid #A9A9A9',
        color: '#A9A9A9',
        fontSize: 12 + 'px',
        width: 200 + 'px',
        height: 30 + 'px'
      },
      hoverProperty: {
        border: 1 + 'px solid #409aff',
      },
      focusProperty: {
        border: 1 + 'px solid #409aff',
        boxShadow: '0 0 0 0.2rem rgba(64,154,255,.15)'
      }
    },
    this.tbInput = this.textboxInput
  },
  mounted () {
    this.placeholderText = this.placeholder
    if (this.mInputProperty && this.mInputProperty.defaultProperty && this.mInputProperty.defaultProperty.constructor === Object) {
      let defaultProp = {...this.inputProperty.defaultProperty, ...this.mInputProperty.defaultProperty}
      this.inputProperty.defaultProperty = defaultProp
    }
    if (this.mInputProperty && this.mInputProperty.hoverProperty && this.mInputProperty.hoverProperty.constructor === Object) {
      let hoverProp = {...this.inputProperty.hoverProperty, ...this.mInputProperty.hoverProperty}
      this.inputProperty.hoverProperty = hoverProp
    }
    if (this.mInputProperty && this.mInputProperty.focusProperty && this.mInputProperty.focusProperty.constructor === Object) {
      let focusProp = {...this.inputProperty.focusProperty, ...this.mInputProperty.focusProperty}
      this.inputProperty.focusProperty = focusProp
    }
  }
}
</script>

<style scoped lang="scss">
  .m-textbox {
    border-radius: 2px;
    padding: 5px;
    &:focus {
      outline: none;
    }
    &::placeholder {
      opacity: 0.5;
    }
  }
</style>
