<script>
import Vue from 'vue'
import VCalendar from 'v-calendar'
Vue.use(VCalendar)

export default {
  name: 'DateFilter',
  data () {
    return {
      range: new Date(),
      high: true,
      newMaxDate: new Date()
    }
  },
  props: {
    showDatePick: {
      type: Boolean,
      default: false
    },
    labelName: {
      type: String,
      default: 'Submit'
    },
    mDate: {
      type: [Object,String,Date],
      default: ''
    },
    maxDate: {
      type: Boolean,
      default: true
    },
    minDate: {
      type: Boolean,
      default: false
    },
    mRangeFilterProperty: {
      type: Object,
      default: () => {
        return {
          textColor: '#4E617B',
          bgPopup: '#fff',
          borderPopup: '#D0D6DD',
          borderInput: '#0978FC',
          fillDateFilter: '#0978FC',
          fillClose: '#D4DAE4'
        }
      }
    }
  },
  watch: {
    mDate () {
      if (!this.mDate) {
        this.range = new Date()
      }
    }
  },
  created () {
    if (!this.mDate) {
      this.range = new Date()
    } else {
        this.range = this.mDate
    }
    if (this.maxDate) {
      this.newMaxDate = new Date()
    } else {
      this.newMaxDate = false
    }
  },
  methods: {
    closeDatePopup () {
      if (event.target === this.$refs.datepop) {
        this.$emit('cancelDate', false)
      }
    },
    cancelPopup () {
      event.preventDefault()
      this.$emit('cancelDate', false)
    },
    getData () {
      event.preventDefault()
      this.$emit('changeFilter', true)
      this.$emit('changeDate', this.range)
      this.$emit('cancelDate', false)
    },
    stopClose () {
      event.stopPropagation()
    },
    displayDate () {
      let start = this.formatDate(this.range)
      return start
    },
    formatDate (val) {
      const months = ['JAN', 'FEB', 'MAR', 'APR', 'MAY', 'JUN', 'JUL', 'AUG', 'SEP', 'OCT', 'NOV', 'DEC']
      let currentDatetime = val
      let d = currentDatetime.getDate() + ',' + months[currentDatetime.getMonth()] + ' ' + currentDatetime.getFullYear()
      return d
    },
    getHighlighted (val) {
      if (val === 'start') {
        this.high = true
      }
    }
  }
}
</script>

